
<template>
  <div class="search">
      <HomeNav></HomeNav>
      <div class="search-box">
          <div class="input-box">
              <input type="text" @input="onInput(keyword)" v-model="keyword" placeholder="搜索歌曲、歌手、专辑" />
              <img class="search-delete" src="../assets/images/search_delete.png" @click="searchDel" alt="">
              <div class="search-btn" @click="search">
                  <img src="../assets/images/search.png" alt="">
              </div>
          </div>
      </div>

      <div class="content-box">
          <div class="text">热门搜索</div>
          
          <div class="item-box" v-if="keyword === ''">
              <span v-for="(item, index) in hotItem" :key="index" @click="hotWord(index)">{{item.first}}</span>
          </div>
          <div class="item-info-box" v-else>
            <div v-for="(item, index) in keywordData" :key="index" class="info-box"  @click="$emit('translate-song', item)" :currentSong="currentSong">
                <div class="item-info">
                    <div class="item-name">{{item.name}}</div>
                    <div class="singer">
                        <i class="sq"></i>
                        <span>{{item.artists[0].name}}</span>
                        <span>-{{item.album.name}}</span>
                    </div>
                </div>
                <span v-if="currentSong && item.id === currentSong.id" class="play">
                    <img src="../assets/images/playing.svg" alt="">
                </span>
                <span v-else class="play">
                    <img src="../assets/images/play.png" alt="">
                </span>
                
            </div>
          </div>
          <!-- <div class="history">
              <div class="history-box">
                <img src="../assets/images/history.png" alt="">
                <div class="history-name">1</div>
                <img src="../assets/images/history_delete.png" alt="">
              </div>
          </div> -->
      </div>
  </div>
</template>

<script>
import HomeNav from '@/components/HomeNav.vue'

    export default {
        name: 'Search',
        components: {
            HomeNav,
        },

        props: {
            currentSong: Object
            // keySearchItem: Object
        },

        data() {
            return {
                hotItem: [],
                keyword: '',
                isSearch: true,
                keywordData: [],
                historyList: [this.keyword]
            }
        },

        methods: {
            getHotItem() {
                this.axios({
                    method: 'GET',
                    url: 'http://music.kele8.cn/search/hot',
                }).then(response => {
                    // 如果数据正确
                    // console.log('response ==>', response);

                    this.hotItem = response.data.result.hots;
                    // console.log('this.hotItem ==>', this.hotItem);

                    // 将数据放进本地存储
                    window.localStorage.setItem('hotItem', JSON.stringify({
                      // 过期时间
                      expire: Date.now() + 1 * 60 * 60 * 1000,
                      result: response.data.result.hots
                    }))
                    
                }).catch(err => {
                    console.log('err ==>', err);
                })

            },

            search() {
                this.axios
                    .get(`https://music.kele8.cn/search?keywords=%20${this.keyword}`, {
                        params: {
                            keyword: this.keyword
                        }
                    })
                    .then(response => {
                    // 如果数据正确
                    console.log(response);
                    console.log(this.keyword);


                    this.keywordData = response.data.result.songs;
                    // console.log("this.keywordData ==>", this.keywordData);

                    // console.log(this.keywordData[0].album.artist.picUrl);
                    

                    // 将数据放进本地存储
                    window.localStorage.setItem(
                        "key" + this.keyword,
                        JSON.stringify({
                            // 过期时间
                            expire: Date.now() + 3 * 60 * 60 * 1000,
                            result: response.data.result.songs,
                        })
                    );
                })
                .catch(err => {
                    console.log("err ==>", err);
                });
                
            },

            hotWord(index) {

                this.index = index;

                // console.log(this.hotItem[index].first);

                this.keyword = this.hotItem[index].first;
                // console.log(this.keyword);
                
            },

            onInput(v) {
                // console.log(v);

                if (v !== '') {

                    this.keyword = v
    
                     this.axios
                        .get(`https://music.kele8.cn/search?keywords=%20${this.keyword}`, {
                            params: {
                                keyword: this.keyword
                            }
                        })
                        .then(response => {
                        // 如果数据正确
                        console.log(response);
                        console.log(this.keyword);
    
    
                        this.keywordData = response.data.result.songs;
                        // console.log("this.keywordData ==>", this.keywordData);
    
                        // console.log(this.keywordData[0].album.artist.picUrl);
                        
    
                        // 将数据放进本地存储
                        window.localStorage.setItem(
                            "key" + this.keyword,
                            JSON.stringify({
                                // 过期时间
                                expire: Date.now() + 3 * 60 * 60 * 1000,
                                result: response.data.result.songs,
                            })
                        );
                    })
                    .catch(err => {
                        console.log("err ==>", err);
                    });
                }

                
            },

            searchDel() {
                this.keyword = ''
            }
        },

        created() {
            // this.getHotItem();
            let keyword = this.keyword

            if (keyword !== '') {
                this.onInput()
                // this.search();

                const localKeyData = JSON.parse(window.localStorage.getItem("key" + this.keyword));

                if (localKeyData && localKeyData.expire > Date.now()) {
                    // 存在且还没有过期
                    this.keyword = localKeyData.result;

                } else {
                    // 已经过期
                    this.search();
                }

            }
            
            

            const localHotsData = JSON.parse(
                window.localStorage.getItem("sl" + this.songListId)
            );

            if (localHotsData && localHotsData.expire > Date.now()) {
                // 存在且还没有过期
                this.hotItem = localHotsData.result;

            } else {
                // 已经过期
                this.getHotItem();
            }
        },

        watch: {
            his(value) {
                this.historyList.push(value);
                console.log(this.historyList.push(value));
                
            }
        }

    }
</script>

